.type-view {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  padding: 10px;
  margin: 10px;
  background-color: #FEDF60;
  border-radius: 10px;
  overflow: auto;
  width: 100%;
}
@media (min-width: 992px) {
  .type-view {
    width: 48%;
    height: 485px;
    margin: 0px;
    -ms-flex-pack: distribute;
    justify-content: space-around;
  }
}



