.App {
  background-color: #FDBB32;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}
.title {
  width: 100%;
  text-align: center;
  margin: 10px 0 10px 0px;
}
.title h1 {
  font-size: 24px;
}
.app-info {
  text-align: center;
  margin: 10px;
}
.cool-item a {
  padding-right: 10px;
}
.strawpoll {
  width: 80vw;
  height: 120vw;
  border: 0;
  border-radius: 10px;
}
.hide {
  width: 0;
  height: 0;
  position: absolute;
  top: 0;
}
.hide a {
  width: 0;
  height: 0;
  position: absolute;
  top: 0;
}
.hide img {
  width: 0;
  height: 0;
  position: absolute;
  top: 0;
}
@media (min-width: 992px) {
  .App {
    width: 800px;
    padding: 10px;
    margin: 50px auto;
    -webkit-box-shadow: 0 14px 45px rgba(0, 0, 0, 0.22), 0 18px 45px rgba(0, 0, 0, 0.22);
    box-shadow: 0 14px 45px rgba(0, 0, 0, 0.22), 0 18px 45px rgba(0, 0, 0, 0.22);
    -ms-flex-pack: distribute;
    justify-content: space-around;
    border-radius: 10px;
  }
  .title {
    margin: 0px 0 10px 0px;
  }
  .strawpoll {
    width: 700px;
    height: 275px;
  }
}



